import { FC, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { ListItem } from "../list-item/list-item";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useShallow } from "zustand/react/shallow";
import { useStore } from "src/store/store";
import {
  filtereData,
  preventDefaultHandler,
  stopPropagationHandler,
} from "src/utils/utils";
import { useLimitScroll } from "src/hooks/use-limit-scroll";
import { useResizeApp } from "src/hooks/use-resize-app";
import { useBlockTouchmove } from "src/hooks/use-block-touchmove";
import classNames from "classnames";
import style from "./list.module.css";

export const List: FC = () => {
  const { t } = useChangeLanguage();

  const listRef = useRef<HTMLDivElement>(null);

  const { overflow, focus, houseData, searchValue, setOverflow } = useStore(
    useShallow(state => ({
      houseData: state.houseData,
      searchValue: state.searchValue,
      focus: state.focus,
      overflow: state.overflow,
      setOverflow: state.setOverflow,
    })),
  );

  const filteredData = filtereData(houseData, searchValue);

  const lowerCaseNumber =
    filteredData && filteredData[0]?.carNumber.toLowerCase();

  useEffect(() => {
    setOverflow("hidden");
    setTimeout(() => {
      setOverflow("auto"),
        listRef.current && listRef.current?.scrollTo({ top: 2 });
    }, 0);
  }, [focus, searchValue, setOverflow]);

  useLimitScroll(listRef);

  useResizeApp();

  useBlockTouchmove();

  return (
    <>
      <div className={style.content__wrapper}>
        <div
          ref={listRef}
          style={{ overflow: overflow }}
          className={style.column__wrapper}
        >
          <div onClick={stopPropagationHandler} className={style.column}>
            <div className={classNames(style.title__row, style.row)}>
              <div>{t("table-number")}</div>
              <div>{t("table-gates")}</div>
            </div>
            {filteredData &&
              filteredData.map(carData => (
                <ListItem carData={carData} key={carData.carNumber} />
              ))}
            {filteredData && filteredData.length === 1 ? (
              <div
                className={
                  !focus
                    ? classNames(style.result__wrapper, style.one)
                    : style.result__wrapper
                }
              >
                <button
                  onMouseDown={preventDefaultHandler}
                  className={style.result__btn}
                >
                  <Link to={`/${lowerCaseNumber}`}>{t("search-finded")}</Link>
                </button>
                <div>{t("search-not-finded")}</div>
              </div>
            ) : (
              <div>{t("search-not-finded")}</div>
            )}
          </div>
        </div>
      </div>
    </>
  );
};
