import { MutableRefObject, useEffect } from "react";

export const useLimitScroll = (ref: MutableRefObject<HTMLElement | null>) => {
  useEffect(() => {
    if (ref.current) {
      const list = ref.current;
      const limitScroll = () => {
        if (list) {
          if (list.scrollHeight - list.scrollTop - list.clientHeight <= 1) {
            list.scrollTop -= 2;
          }
          if (list.scrollTop === 0) {
            list.scrollTop += 2;
          }
        }
      };
      list && list.addEventListener("scroll", limitScroll);
      return () => {
        list && list.removeEventListener("scroll", limitScroll);
      };
    }
  }, [ref]);
};
