import { FC } from "react";
import { Link } from "react-router-dom";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useShallow } from "zustand/react/shallow";
import { useStore } from "src/store/store";
import style from "./search-form.module.css";

interface SearchFormProps {
  query?: {
    id: number;
    name: string;
    query: string;
  };
}

export const SearchForm: FC<SearchFormProps> = props => {
  const { query } = props;

  const { t } = useChangeLanguage();

  const { searchValue, setSearchValue, focusHandler, blurHandler } = useStore(
    useShallow(state => ({
      searchValue: state.searchValue,
      focusHandler: state.focusHandler,
      blurHandler: state.blurHandler,
      setSearchValue: state.setSearchValue,
    })),
  );

  return (
    <>
      <div id={"search"} className={style.search__wrapper}>
        <span className={style.line}></span>
        <input
          type="number"
          placeholder={t("search-field")}
          inputMode="numeric"
          pattern="[0-9]*"
          className={style.search__input}
          value={searchValue}
          onInput={setSearchValue}
          onFocus={focusHandler}
          onBlur={blurHandler}
        />
        <p className={style.search__info}>{t("search-notice")}</p>
        <Link className={style.search__link} to={`/${query?.query}/schema`}>
          {t("search-question")}
        </Link>
      </div>
    </>
  );
};
