import { useEffect } from "react";
import { useStore } from "src/store/store";
import { useShallow } from "zustand/react/shallow";

export const useBlockTouchmove = () => {
  const { focus } = useStore(
    useShallow(state => ({
      focus: state.focus,
    })),
  );

  useEffect(() => {
    const blockHandler = (e: Event) => {
      e.preventDefault();
    };

    if (focus) {
      document
        .querySelector("header")
        ?.addEventListener("touchmove", blockHandler);
      document
        .querySelector("#search")
        ?.addEventListener("touchmove", blockHandler);
    }

    return () => {
      document
        .querySelector("header")
        ?.removeEventListener("touchmove", blockHandler);
      document
        .querySelector("#search")
        ?.removeEventListener("touchmove", blockHandler);
    };
  }, [focus]);
};
