import { FC, Fragment } from "react";
import { Link } from "react-router-dom";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { onMouseDownLinkHandler, splitCarNumber } from "src/utils/utils";
import { useStore } from "src/store/store";
import { useShallow } from "zustand/react/shallow";
import { ICar } from "src/api/car";
import style from "./list-item.module.css";

export const ListItem: FC<{ carData: ICar }> = ({ carData }) => {
  const { searchValue } = useStore(
    useShallow(state => ({
      searchValue: state.searchValue,
    })),
  );

  const { t, translation } = useChangeLanguage();

  const carNumber = splitCarNumber(carData.carNumber, searchValue);

  return (
    <>
      <Link
        onMouseDown={onMouseDownLinkHandler}
        to={`/${carData.carNumber.toLowerCase()}`}
      >
        <div className={style.row} key={carData.carsQuantity}>
          <div className={style.car__number}>
            {carData.carsQuantity !== null && (
              <span className={style.car__quantity}>
                {`${carData.carsQuantity}.`}
              </span>
            )}
            {carNumber.map((part, partIndex) => {
              return (
                <Fragment key={partIndex}>
                  {partIndex === 1 ? (
                    <span className={style.highlighted}>{searchValue}</span>
                  ) : (
                    partIndex > 0 && searchValue
                  )}
                  {part}
                </Fragment>
              );
            })}
          </div>
          {carData.gateName ? (
            <div className={style.gate__number}>â†’ {carData.gateName}</div>
          ) : (
            <div
              className={
                translation !== "tj"
                  ? style.gate__notAssigned
                  : style.tj__notAssigned
              }
            >
              {t("wait")}
            </div>
          )}
        </div>
      </Link>
    </>
  );
};
