import { FC, useEffect } from "react";
import { Layout } from "src/components/layout/layout";
import { Loader } from "src/components/loader/loader";
import { List } from "src/components/list/list";
import { SearchForm } from "src/components/search-form/search-form";
import { useStore } from "src/store/store";
import { useShallow } from "zustand/react/shallow";
import { useAnalitics } from "src/hooks/use-analitics";
import { findWarehouse } from "src/utils/find-warehouse";
import style from "./warehouse-list-page.module.css";

const WarehouseListPage: FC = () => {
  const { houseData, fetchHouseData } = useStore(
    useShallow(state => ({
      houseData: state.houseData,
      fetchHouseData: state.fetchHouseData,
    })),
  );

  const query = findWarehouse(houseData);

  useEffect(() => {
    if (query) {
      fetchHouseData(query);
    }

    window.scrollTo({ top: 0 });
  }, [fetchHouseData, query]);

  useAnalitics();

  return (
    <Layout>
      {houseData && houseData.length >= 0 ? (
        <div className={style.wrapper}>
          <List />
          <SearchForm query={query} />
        </div>
      ) : (
        <Loader />
      )}
    </Layout>
  );
};
export default WarehouseListPage;
